@media (min-width: 1280px){.container{width:1280px;padding-top:20px}.table{display:table;position:fixed;width:100%;height:100%;top:0;left:0}.table.table_full{width:100%;table-layout:fixed}.table__cell{display:table-cell;vertical-align:top}.table__cell.table__cell_middle{vertical-align:middle}.table__cell+.table__cell{border-left:1px solid rgba(0,0,0,0.5);-webkit-box-shadow:inset 5px 0 10px -5px rgba(0,0,0,0.5);box-shadow:inset 5px 0 10px -5px rgba(0,0,0,0.5)}.nav__item{font-size:21px;padding:20px}.step{min-height:400px;padding:100px 0}.step h2{font-size:48px;padding-bottom:20px}button,.button{margin-bottom:15px}.step__list li{width:100px;height:100px}.triangle{border:60px solid transparent;border-top-width:0;border-bottom:95px solid rgba(0,0,0,0.3)}.square,.circle{width:100px;height:100px}.result{padding:50px 0}.result__case{min-height:200px}.result__case h3{padding-top:120px}.done .result__case{-webkit-transform:scale(1.3);-ms-transform:scale(1.3);transform:scale(1.3)}}
